@charset "utf-8";

.form {display: flex;}
.form input {flex: 1; font-size: 1.25rem; outline: none; border: none; border-bottom: 1px solid #ff8fb4; background-color: #fff;}
.create-button {padding: 0.5rem 1rem; margin-left: 1rem; background-color: #ff8fb4; border-radius: 3px; color: #fff; font-weight: bold; cursor: pointer;}
.create-button:hover {background-color: #ffd7e4;}